.StopWatch_container__1k0IV{
    text-align: center;
}   
.StopWatch_display__2sYd7{
    width: 500px;
    background-color: aqua;
    text-align: center;
    font-size: 70px;
    display: contents;
}

.StopWatch_button__CfJoW{
    text-decoration: none;
    display: inline-block;
    padding: 10px 30px;
    margin: 10px 20px;
    position: relative;
    overflow: hidden;
    border: 2px solid #181211;
    border-radius: 8px;
    font-family: 'Montserrat', sans-serif;
    color: #181211;
    transition: .2s ease-in-out;
    outline: none;
}

.StopWatch_button__CfJoW:before {
    content: "";
    background: linear-gradient(90deg, rgba(255, 255, 255, .1), rgba(255, 255, 255, .5));
    height: 50px;
    width: 50px;
    position: absolute;
    top: -8px;
    left: -75px;
    transform: skewX(-45deg);
}
.StopWatch_stop_button__1wbe_{
    background-color: red;
    color: white;
}
.StopWatch_button__CfJoW:hover {
    background: #181211;
    color: #fff;
}
.StopWatch_button__CfJoW:hover:before {
    left: 150px;
    transition: .5s ease-in-out;
}
